.app__products {
    display: flex;
    flex-direction: column;

    overflow: hidden;
}

.app__products-title {
    text-align: center;
}


.app__products-content {
    width: 100%;
}

.app__products-item-container {
    width: 1200px;
}

.app__products-item {
    width: 100%;
    display: flex;
    align-items: center;
    margin-top: 10rem;
    opacity: 0;

    transition: 1s ease;
}


.app__products-item:nth-child(1) {
    transform: translateX(2000px);
}

.app__products-item:nth-child(2) {
    flex-direction: row-reverse;
    transform: translateX(-2000px);
}

.app__products-item:nth-child(3) {
    transform: translateX(2000px);
}

.product-animation {
    width: 100%;
    display: flex;
    align-items: center;
    margin: 8rem 0;

    transform: translateX(0);
    opacity: 1;
    transition: 1s ease;
}

.product-animation-reverse {
    width: 100%;
    display: flex;
    align-items: center;
    margin: 8rem 0;
    flex-direction: row-reverse;

    transform: translateX(0);
    opacity: 1;
    transition: 1s ease;
}


.app__products-info {
    width: 600px;
    text-align: center;
    
    letter-spacing: 1.5px;
    text-shadow: 0 0 3px black, 0 0 9px grey;
    margin: 2rem;
}

.app__products-info > img {
    width: 55px;
}

.app__products-info-title {
    font-family: var(--font-base);
    font-size: 38px;
    color: var(--color-white);
}

.app__products-info-p {
    font-family: var(--font-base);
    font-size: 24px;
    font-weight: bold;
    color: var(--color-white);
    line-height: 38px;

    margin-top: 1rem;
}



@media screen and (max-width:1150px) {
    .product-animation {
        flex-direction: column;

        margin: 4rem 0;
    }

    .product-animation-reverse {
        flex-direction: column;

        margin: 4rem 0;
    }

    .app__products-item:nth-child(2) {
        flex-direction: column;
    }
    
}

@media screen and (max-width:650px) {
    .app__products-item-container {
        width: auto;
    }

    .product-animation {
        margin: 3rem 0;
    }

    .product-animation > img {
        width: 350px;
    }

    .product-animation-reverse {
        margin: 3rem 0;
    }

    .product-animation-reverse > img {
        width: 350px;
    }

    .app__products-info {
        width: 350px;
        margin: 2rem 0;
    }

    .app__products-info-title {
        font-size: 26px;
    }

    .app__products-info-p {
        font-size: 15px;
        line-height: 25px;
    }
}