.app__founder-image {
    width: 80%;
    display: flex;
    justify-content: center;
}

.app__wrapper_info {
    margin-top: 3rem;
}

.app__founder-content {
    display: flex;
    flex-direction: column;
    width: 100%;

    margin-top: 3rem;
}

.app__founder-content_quote {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}

.app__founder-content_quote > img {
    width: 47px;
    height: 40px;
    margin: 0 1rem 0.5rem 0;
}

.app__founder-sign {
    width: 100%;
    margin-top: 3rem;
}

.app__founder-sign p:first-child {
    font-family: var(--font-base);
    font-weight: 600;
    font-size: 32px;
    line-height: 41px;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    color: var(--color-golden);
}

.app__founder-sign > img {
    width: 300px;
    margin-top: 1rem;
}


@media screen and (max-width: 2000px) {
    .app__founder-sign > img {
        width: 370px;
    }
}

@media screen and (max-width: 650px) {
    .app__founder-image {
        width: 100%;
    }
    
    .app__founder-content {
        margin-top: 2rem;
    }

    .app__founder-sign > img {
        width: 80%;
    }
}